import { Component, effect, OnInit } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonHeader,
  IonTitle,
  IonToolbar,
  IonList,
  IonItem,
  IonLabel,
  IonText,
  IonIcon,
  IonNote,
  IonBadge,
  IonRow,
  IonCol,
  IonItemGroup,
  IonItemDivider,
  IonProgressBar,
  IonRefresher,
  IonButton,
  IonSkeletonText,
  IonThumbnail,
} from '@ionic/angular/standalone'
import { HeaderComponent } from 'src/app/components/header/header.component'
import {
  AbonnementsState,
  AbonnementWrapperService,
} from 'src/app/services/api-wrappers/abonnement.service'
import { TranslateModule } from '@ngx-translate/core'
import { SubscriptionItemComponent } from './components/subscription-item.component'

@Component({
  selector: 'app-abonnements',
  templateUrl: './abonnements.page.html',
  styleUrls: ['./abonnements.page.scss'],
  imports: [
    CommonModule,
    FormsModule,
    HeaderComponent,
    TranslateModule,
    IonContent,
    IonList,
    IonItem,
    IonLabel,
    IonSkeletonText,
    IonThumbnail,
    IonRow,
    IonCol,
    IonProgressBar,
    IonRefresher,
    SubscriptionItemComponent,
  ],
})
export class AbonnementsPage implements OnInit {
  readonly skeletonCount = 5
  abonnementState: AbonnementsState = { state: 'loading' }

  constructor(public abonnementService: AbonnementWrapperService) {
    effect(() => {
      this.abonnementState = this.abonnementService.listAbonnementsState()
    })
  }

  ngOnInit() {
    this.refresh()
  }

  handleRefresh(event: any) {
    this.refresh().then(() => {
      event.target.complete()
    })
  }

  refresh(): Promise<void> {
    return new Promise<void>((resolve) => {
      this.abonnementService.getAbonnements()
      resolve()
    })
  }

  getCurrentSubscription() {
    const currentAbonnement = this.abonnementService.currentAbonnementState()
    if (
      currentAbonnement.state !== 'unknown' &&
      currentAbonnement.state !== 'default'
    ) {
      return currentAbonnement.abonnement
    }
    return null
  }

  getPreviousSubscriptions() {
    return this.abonnementState.state === 'success'
      ? this.abonnementState.abonnements.filter(
          (abo) => abo.status === 'canceled'
        )
      : []
  }
}
