import { Component, Input } from '@angular/core'
import { ListAbonnement200ResponseAllOfDataInner } from 'src/api-cyclist'
import {
  IonItem,
  IonIcon,
  IonLabel,
  IonText,
  IonBadge,
  IonButton,
  IonRow,
  IonModal,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonButtons,
  IonImg,
} from '@ionic/angular/standalone'
import { CommonModule, DatePipe } from '@angular/common'
import { addIcons } from 'ionicons'
import { cardOutline, close } from 'ionicons/icons'
import { AbonnementWrapperService } from 'src/app/services/api-wrappers/abonnement.service'
import { TranslateModule } from '@ngx-translate/core'

@Component({
  selector: 'app-subscription-item',
  imports: [
    IonItem,
    IonIcon,
    IonLabel,
    IonText,
    CommonModule,
    IonButton,
    DatePipe,
    TranslateModule,
    IonRow,
    IonModal,
    IonContent,
    IonImg,
  ],
  template: `
    <ion-item color="grey">
      <ion-label class="ion-text-wrap">
        <ion-row class="ion-justify-content-between">
          <strong>{{ subscription.forfait?.name }}</strong>
          <p>{{ subscription.amount! / 100 }}â‚¬</p>
        </ion-row>
        <ion-row class="ion-justify-content-between">
          <p *ngIf="subscription.canceled_at">
            {{
              'subscription_item.date_range'
                | translate
                  : {
                      start: subscription.created! * 1000 | date : 'dd/MM/yyyy',
                      end:
                        subscription.canceled_at! * 1000 | date : 'dd/MM/yyyy'
                    }
            }}
          </p>
          <p *ngIf="!subscription.canceled_at">
            {{
              'subscription_item.since_date'
                | translate
                  : {
                      date: subscription.created! * 1000 | date : 'dd/MM/yyyy'
                    }
            }}
          </p>
        </ion-row>
        @if(subscription.supporting_documents!.length > 0) {
        <ion-row>
          <p>{{ 'subscription_item.supporting_documents' | translate }}:</p>
        </ion-row>
        <ul class="ion-no-padding">
          <li *ngFor="let document of subscription.supporting_documents">
            <ion-row *ngIf="document.name">
              <ion-text color="medium">-{{ document.name }}</ion-text>
              <a
                fill="clear"
                (click)="openImage(document.front_image)"
                *ngIf="document.front_image"
                class="ion-margin-horizontal"
              >
                {{ 'subscription_item.front_side' | translate }}
              </a>
              <ion-text color="primary">|</ion-text>
              <a
                (click)="openImage(document.back_image)"
                *ngIf="document.back_image"
                class="ion-margin-start"
              >
                {{ 'subscription_item.back_side' | translate }}
              </a>
            </ion-row>
          </li>
        </ul>

        } @if(subscription.status === 'active' || subscription.status ===
        'waiting_validation') {
        <ion-row class="ion-justify-content-end">
          <ion-button
            color="danger"
            fill="outline"
            size="small"
            (click)="AbonnementWrapperService.requestDeletion()"
          >
            {{ 'subscription_item.terminate' | translate }}
          </ion-button>
        </ion-row>
        }
      </ion-label>
    </ion-item>
    <ion-modal
      [isOpen]="isModalOpen"
      (didDismiss)="isModalOpen = false"
      class="image-modal"
    >
      <ng-template>
        <ion-content class="ion-padding">
          <ion-button (click)="isModalOpen = false" fill="clear" color="dark">
            <ion-icon name="close"></ion-icon>
          </ion-button>
          <ion-img [src]="currentImageUrl" />
        </ion-content>
      </ng-template>
    </ion-modal>
  `,
  styles: [
    `
      ion-modal {
        --height: 50%;
        --border-radius: 16px;
        --box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1),
          0 4px 6px -4px rgb(0 0 0 / 0.1);
      }

      ion-modal::part(backdrop) {
        background: rgba(209, 213, 219);
        opacity: 1;
      }

      ion-img::part(image) {
        aspect-ratio: 4/3;
        width: 100%;
        object-fit: contain;
      }
    `,
  ],
})
export class SubscriptionItemComponent {
  @Input() subscription!: ListAbonnement200ResponseAllOfDataInner
  isModalOpen = false
  currentImageUrl = ''

  constructor(public AbonnementWrapperService: AbonnementWrapperService) {
    addIcons({ cardOutline, close })
  }

  openImage(imageUrl: string) {
    this.currentImageUrl = imageUrl
    this.isModalOpen = true
  }
}
